/*Student DataBase Tables*/

.student-main {
  display: flex;
  justify-content: space-evenly;
}

.student-body {
  margin: 1.5rem;
  padding: 1rem;
  background-color: #e0e0e00b;
  border: none;
  border-radius: 10px;
  box-shadow: 0 0 15px 10px rgba(0, 0, 0, 0.058);
}

.student-body1 {
  display: flex;
  justify-content: center;
  align-items: center;
  width: 500px;
  height: 300px;
  margin: 1.5rem;
  padding: 1rem;
  background-color: #e0e0e00b;
  font-weight: bolder;
  border: none;
  border-radius: 10px;
  box-shadow: 0 0 15px 10px rgba(0, 0, 0, 0.058);
}
.student-title h2 {
  color: #0077ff;
  text-shadow: 2px 2px rgba(0, 0, 0, 0.111);
}

.table-container {
  width: 100%;
  overflow-x: auto;
}

.table {
  border-collapse: collapse;
  border-spacing: 0;
  border-radius: 3%;
  table-layout: fixed;
  text-align: center;
}

.table th,
td {
  border: 1px solid rgba(0, 0, 0, 0.198);
  padding: 10px;
  word-wrap: break-word;
  white-space: nowrap;
}
.table th {
  background-color: #0077ff;
  color: #fff;
}

.table tr:nth-child(even) {
  background-color: #f2f2f2;
}

.table tr:hover {
  background-color: rgba(209, 208, 208, 0.552);
}

/*Student DataBase btn*/

.btn-delete-student {
  float: right;
  padding: 0.6rem 1.4rem;
  border: none;
  border-radius: 8px;
  background: #ec2c2cea;
  color: #fff;
  font-size: 1rem;
  font-weight: 500;
  cursor: pointer;
  transition: background 0.3s;
}

.btn-delete-student:hover {
  background: #9b2222ec;
}

.btn-update-student {
  float: right;
  padding: 0.6rem 1.4rem;
  border: none;
  border-radius: 8px;
  background: #0077ff;
  color: #fff;
  font-size: 1rem;
  font-weight: 500;
  cursor: pointer;
  transition: background 0.3s;
}

.btn-update-student:hover {
  background: #005bc2;
}

/*end Student DataBase Tables*/
